<template>
	<gracePage :customHeader="false">
		<view slot="gBody" class="grace-body">
			<view class="grace-margin-top grace-margin-bottom"><rich-text type="text" :nodes="aboutHtml"></rich-text></view>
			<view class="ucenter-line"></view>
			<view class="grace-margin-top grace-margin-bottom"><rich-text type="text" :nodes="contactHtml"></rich-text></view>
			<view class="ucenter-line"></view>
			<view class="img grace-margin-top grace-margin-bottom"><image src="../../static/img/5steps.png"></image></view>
			<view class="grace-flex-center grace-wrap"><navigator open-type="reLaunch" url="../main/main" class="grace-padding">返回首页</navigator></view>
		</view>
	</gracePage>
</template>

<script>
var graceRichText = require('../../graceUI/jsTools/richText.js');
export default {
	data() {
		return {
			aboutHtml: '',
			contactHtml: '',
			processHtml: ''
		};
	},
	onLoad: function() {
		let me = this;
		let serverUrl = me.getServerUrl();
		uni.request({
			url: serverUrl + '/AppContent/Get?pageCode=about',
			method: 'POST',
			header: {
				appKey: me.appKey,
				appSecret: me.appSecret
			},
			success: res => {
				if (res.data.Status) {
					if (res.data.Data == null) {
					} else {
						this.aboutHtml = graceRichText.format(res.data.Data.Description);
					}
				} else {
					uni.showToast({
						title: res.data.StatusMessage,
						duration: 2000,
						icon: 'none'
					});
				}
			},
			fail: function() {
				uni.showToast({
					title: '网络异常请重试',
					duration: 2000,
					icon: 'none'
				});
			},
			complete: function() {
				setTimeout(function() {
					uni.hideLoading();
				}, 2000);
			}
		});

		uni.request({
			url: serverUrl + '/AppContent/Get?pageCode=contact',
			method: 'POST',
			header: {
				appKey: me.appKey,
				appSecret: me.appSecret
			},
			success: res => {
				if (res.data.Status) {
					if (res.data.Data == null) {
						this.contactHtml = '';
					} else {
						this.contactHtml = graceRichText.format(res.data.Data.Description);
					}
				} else {
					uni.showToast({
						title: res.data.StatusMessage,
						duration: 2000,
						icon: 'none'
					});
				}
			},
			fail: function() {
				uni.showToast({
					title: '网络异常请重试',
					duration: 2000,
					icon: 'none'
				});
			},
			complete: function() {
				setTimeout(function() {
					uni.hideLoading();
				}, 2000);
			}
		});
	},
	onShow() {
		// #ifdef MP-WEIXIN
		if (wx.hideHomeButton) {
			wx.hideHomeButton();
		}
		// #endif
	},
	methods: {
		makePhoneCall(phoneNumber) {
			uni.makePhoneCall({
				phoneNumber: phoneNumber
			});
		},
		onNavigationBarButtonTap(e) {
			if (e.type == 'back') {
				uni.reLaunch({
					url: '../main/main'
				});
			}
		}
	},
	onShareAppMessage(res) {
		return {
			title: '盘点App-联系我们',
			path: '/pages/user/contactus'
		};
	}
};
</script>

<style>
.ucenter-line {
	width: 100%;
	height: 10px;
	background: #f4f5f6;
	margin: 8px 0;
}
</style>
